import React from 'react';

const Modal = ({ isOpen, onClose, nft }) => {
  if (!isOpen || !nft) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
      <div className="bg-white rounded-lg p-4 w-96">
        <h2 className="text-2xl font-bold">{nft.name}</h2>
        <img src={nft.image} alt={nft.name} className="w-full h-auto mb-4" />
        <p><strong>Price:</strong> {nft.price || 'N/A'} SOL</p>
        <p><strong>Metadata:</strong> {JSON.stringify(nft.metadata, null, 2)}</p>
        <button
          onClick={onClose}
          className="mt-4 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-700"
        >
          Close
        </button>
      </div>
    </div>
  );
};

export default Modal;
